import { StatusBar } from 'expo-status-bar';
import React, {useState, useEffect} from 'react';
import { StyleSheet, Text, View, Button } from 'react-native';
import { Camera } from 'expo-camera';
import { BarCodeScanner } from 'expo-barcode-scanner';
import * as Permissions from 'expo-permissions';

export default function App() {
  const [permission, askForPermission] = Permissions.usePermissions(Permissions.CAMERA, {ask: true});
  const [type, setType] = useState(Camera.Constants.Type.back);
  const [scanned, setScanned] = useState(false);

  const handleScanned = ({type, data}) => {
    setScanned(true);
    alert(`Get: ${data}`);
  }

  if (!permission || permission.status !== 'granted') {
    return (
      <View>
        <Text>No access permission to camera</Text>
        <Button title="Get permission" onPress={askForPermission} />
      </View>
    );
  }
  return (
    <View style={styles.container}>
      <Camera
        style={styles.camera}
        type={type}
        // focusDepth={0.5}
        zoom={0.3}
        barCodeScannerSettings={{
          barCodeTypes: [BarCodeScanner.Constants.BarCodeType.qr],
        }}
        onBarCodeScanned={scanned ? undefined : handleScanned}
      >
        <Text>0.0.8</Text>
      </Camera>
      {scanned && <Button title={'Try again'} onPress={()=>{setScanned(false)}} />}
      {!scanned && <Button title={'waiting...'} onPress={()=>alert("version: 0.0.9")} />}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1
  },
  camera: {
    flex: 1
  }
});
